<template>
<div class="music">
  <audio src="http://qn.media.epub360.com/materials/audio/30062655087f4b6c7bb799cce923196d.mp3" preload>

    </audio>
</div>
</template>

<script>
export default {
  methods: {
    playMusic() {
      console.log(this.$el.querySelector('audio'));
      let $player = this.$el.querySelector('audio')
      $player.currentTime = 0
      $player.play()
    }
  }
}
</script>

<style lang="scss">
</style>



