<template>
  <div id="app">
    <v-flash @play="handlePlay"></v-flash>
    <v-music ref="music"></v-music>
  </div>
</template>

<script>
import vFlash from './components/flash'
import vMusic from './components/music'
export default {
  name: 'app',
  components: {
    vFlash,
    vMusic
  },
  methods: {
    handlePlay(){
      console.log(this.$refs.music.playMusic());
    }
  }
}
</script>

<style lang="scss">
@import "~@/assets/scss/base.scss";
html,body{
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}
#app{
  width: 100%;
  height: 100%;
}
</style>



