<template>
<div class="flash">
  <div class="frame" v-for="(copyItem, index) in copy" :key="index">
    <template v-if="copyItem.type=='split'">
        <div class="line-container split-container" v-for="(word, wordIndex) in copyItem.text.split(' ')" :key="wordIndex">
          <div class="line">
            <span class="letter" v-for="(letter, letterIndex) in word" :key="letterIndex">{{letter}}</span>
          </div>
        </div>
      </template>
    <template v-if="copyItem.type=='normal'">
        <div class="line-container normal-container">
          <div class="line">
            <span class="letter" v-for="(letter, letterIndex) in copyItem.text" :key="index" :class="{'block-span':letter == copyItem.br}">
              {{letter}}
            </span>
          </div>
        </div>
      </template>
    <template v-if="copyItem.type=='fullscreen'">
      <div class="line-container snow-container">
        <span class="snow" v-for="(snowItem, index) in 150" :key="index">{{copyItem.word}}</span>
      </div>
        <div class="line-container fullscreen-container">
          <div class="line bg-line">
            <span class="letter" v-for="(letter, letterIndex) in copyItem.text" :key="letterIndex">{{letter}}</span>
          </div>
        </div>
      </template>
    <template v-if="copyItem.type=='price'">
        <div class="line-container price-container">
          <div class="line">
            <div class="price">{{copyItem.price}}</div>
            <span class="letter" v-for="(letter, letterIndex) in copyItem.text" :key="letterIndex">{{letter}}</span>
          </div>
        </div>
      </template>
  </div>
  <div class="play-btn" @click="play" v-show="showPlayBtn">
    <span v-if="!played">现在开始</span>
    <span v-else>重播</span>
  </div>
</div>
</template>

<script>
import TweenMax from 'gsap'
import TimelineMax from 'gsap/TimelineMax'
import {
  copy
} from '@/assets/copy.js'
import $ from 'webpack-zepto'

function getRandom(max, min) {
  return Math.floor(Math.random() * (1 + max - min) + min);
}
export default {
  name: 'flash',
  data() {
    return {
      copy: copy,
      tl: new TimelineMax(),
      showPlayBtn: true,
      played: false,
      scale: 0.6
    }
  },
  mounted() {
    this.initPage()
  },
  methods: {
    showLetter($line) {
      let $letters = $line.find('.letter')
      $letters.each((index, $el) => {
        this.tl.add(TweenMax.to($el, .3, {
          autoAlpha: 1,
          scale: 1,
          ease: Back.easeOut
        }), '-=.2')
      })
      this.tl.add(TweenMax.to($line, .1, {
        autoAlpha: 0,
        x: -50
      }), '+=.5')
    },
    showLetterFrame($frame) {
      this.tl.add(() => {
        $(this.$el)
          .removeClass('reverse')
        TweenMax.set($frame, {
          autoAlpha: 1
        })
      })
      this.showLetter($frame
        .find('.line'))
    },
    showScaleFrame($frame, magnify) {
      this.tl.add(() => {
        TweenMax.set($frame, {
          autoAlpha: 1
        })
      }, '-=.2')
      this.scale = 0.6
      $frame.find('.line')
        .each((index, $el) => {
          this.tl.add(() => {
              index && $(this.$el)
                .toggleClass('reverse')
              TweenMax.fromTo($el, .3, {
                autoAlpha: 0,
                scale: 5
              }, {
                autoAlpha: 1,
                scale: magnify ? (this.scale *= 1.5) : 1,
                ease: Back.easeOut
              })
            })
            .add(() => {
              TweenMax.set($el, {
                autoAlpha: 0
              })
            }, '+=.7')
        })
    },
    showSnowFrame($frame) {
      this.tl.add(() => {
        $(this.$el)
          .removeClass('reverse')
        TweenMax.set($frame, {
          autoAlpha: 1
        })
      })
      this.tl.add(() => {
        TweenMax.to($frame.find('.snow-container .snow'), .3, {
          // rotationY: 0,
          autoAlpha: 1,
          x: 0,
          ease: Cubic.easeOut
        })
      }, '-=.1')
      this.tl.add(TweenMax.fromTo($frame.find('.fullscreen-container'), .3, {
          autoAlpha: 0,
          scale: 3
        }, {
          autoAlpha: 1,
          scale: 1,
          ease: Back.easeOut
        }), '+=.2')
        .add(TweenMax.to($frame, .2, {
          autoAlpha: 0
        }), '+=.6')

    },
    showPriceFrame($frame) {
      this.tl.add(() => {
        $(this.$el)
          .removeClass('reverse')
        TweenMax.set($frame, {
          autoAlpha: 1
        })
      })
      this.tl.add(TweenMax.fromTo($frame.find('.price'), .3, {
        autoAlpha: 0,
        scale: 5
      }, {
        autoAlpha: 1,
        scale: 1,
        ease: Back.easeOut
      }))
      this.showLetter($frame.find('.line'))
      this.tl.add(TweenMax.to($frame, .2, {
        autoAlpha: 0
      }), '-=.1')
    },
    initDom() {
      let $frames = $(this.$el)
        .find('.frame')
      TweenMax.set($(this.$el)
        .find('.split-container .line'), {
          autoAlpha: 0,
          scale: 0
        })
      TweenMax.set($(this.$el)
        .find('.normal-container .letter'), {
          autoAlpha: 0,
          scale: 0
        })
      TweenMax.set($frames.find('.fullscreen-container'), {
        autoAlpha: 0,
        scale: 0
      })
      $frames.find('.snow-container .snow')
        .each((index, $el) => {
          TweenMax.set($el, {
            // rotationY: getRandom(-360, 600),
            x: getRandom(-600, 600),
            autoAlpha: 0
          })
        })
      TweenMax.set($frames.find('.price-container .price'), {
        autoAlpha: 0,
        scale: 0
      })
      TweenMax.set($frames.find('.price-container .letter'), {
        autoAlpha: 0,
        scale: 0
      })

      TweenMax.set($frames, {
        autoAlpha: 0
      })
      TweenMax.set($frames[0], {
        autoAlpha: 1
      })
    },
    initPage() {
      this.$nextTick(() => {
        let $frames = $(this.$el)
          .find('.frame')
        this.initDom()

        this.tl.add(TweenMax.to($frames.eq(0)
            .find('.line')[0], .5, {
              autoAlpha: 1,
              scale: 1.5,
              ease: Elastic.easeOut
            }))
          .add(TweenMax.to($frames.eq(0)
            .find('.line')[0], .1, {
              autoAlpha: 0,
              x: -100
            }), '+=.4')
        for (let i = 1; i < 6; i++) {
          this.tl.add(TweenMax.to($frames.eq(0)
              .find('.line')[i], .3, {
                autoAlpha: 1,
                scale: 2,
                ease: Back.easeOut
              }), '-=.1')
            .add(TweenMax.to($frames.eq(0)
              .find('.line')[i], .1, {
                autoAlpha: 0,
              }), '-=.01')
        }
        this.showLetterFrame($frames.eq(1))
        this.showLetterFrame($frames.eq(2))
        this.showLetterFrame($frames.eq(3))
        this.showScaleFrame($frames.eq(4))
        this.showSnowFrame($frames.eq(5))
        this.showSnowFrame($frames.eq(6))
        this.showSnowFrame($frames.eq(7))
        this.showPriceFrame($frames.eq(8))
        this.showPriceFrame($frames.eq(9))
        this.showPriceFrame($frames.eq(10))
        this.showScaleFrame($frames.eq(11))
        this.showScaleFrame($frames.eq(12), true)
        this.tl.add(() => {
          this.initDom()
          this.played = true
          this.showPlayBtn = true
          this.scale = 0.6
        }, '+=.1')
        this.tl.pause()
      })

    },
    play() {
      this.showPlayBtn = false
      this.$emit('play')
      if (this.played) {
        this.tl.restart()
      } else {
        this.tl.play()
      }
    }
  }
}
</script>

<style lang="scss">
@import "~@/assets/scss/variables.scss";
.flash {
    width: 100%;
    height: 100%;
    position: relative;
    background-color: $bg;
    color: $text;
    overflow: hidden;
    transition: all 0.2s ease-out;
    &.reverse {
        background-color: $text;
        color: $bg;
    }
    .play-btn {
        width: 200px;
        height: 50px;
        color: #fff;
        font-size: 2em;
        text-align: center;
        position: absolute;
        top: 50%;
        left: 50%;
        margin-left: -100px;
        margin-top: -25px;
        z-index: 9;
    }
    .frame {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        span {
            display: inline-block;
            &.block-span {
                display: block;
                line-height: 0.8em;
            }
        }
        .snow-container {
            font-size: 1.5em;
            text-align: center;
            .snow {
                margin: 3px;
            }
        }
        .line-container {
            display: table;
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            .line {
                text-align: center;
                display: table-cell;
                vertical-align: middle;
                font-size: 4em;
                &.bg-line {
                    background-color: $bg;
                    background-clip: content-box;
                }
            }
        }
    }
}
</style>



